
nav {
	background-color: #324157;
}
.menu-ul {
	max-width: 1200px;
	margin: 0 auto !important;
}
.nav-logo {
	float: left;
	width: 90px;
	height: 60px;
}
.nav-logo a:visited,
.nav-logo a:link {
	color: #FFFFFF;
	line-height: 60px;
	font-size: 22px;
}
/*.nav-logo img {
	max-width: 60px;
	max-height: 40px;
	padding: 10px 15px;
}*/
.nav-search {
	float: left;
	/*line-height: 60px;*/
	/*ie9下不行*/
	padding: 12px 10px;
}
.nav-search .el-input__inner {
	border-radius: 40px;
	outline: none;
}
.nav-user {
	float: right;
}
.nav-user a.user-icon {
}
.nav-user .user-icon img {
	margin: 10px 0 0px;
	width: 40px;
	height: 40px;
	border-radius: 50%;
}
.published {
	float: right;
	line-height: 60px;
	padding: 0 0px 0 15px;
}
.not-login {
	float: right;
}
.not-login a {
	color: #bfcbd9;
	line-height: 60px;
	padding: 0 5px;
	font-size: 13px;
}



